* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    /*max-width: 400px;*/
    width: 100%;
    margin: 20px auto;
    background-color: crimson;
}

.title__count {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding-top: 40px;
}

.title__str{
    margin-top: 20px;
    justify-content: center;
    align-items: center;
    font-size: 24px;
    width: 100%;
    display: flex;
}

button {
    padding: 10px 20px;
    border: 1px solid black;
    margin-right: 15px;
    border-radius: 5px;
}

button:last-child {
    margin-right: 0;
}

button:hover {
    cursor: pointer;
}

.btn__block {
    padding: 20px 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

nav {
    width: 100%;
    display: flex;
    padding: 20px 0;
    /*margin: 0 auto;*/
    background-color: teal;
}

.nav__link {
    margin: 0 auto;
    padding: 10px 20px;
    border: 1px solid black;
    cursor: pointer;
    text-decoration: none;
    color: black;
}

.nav__link:hover {
    border: 1px solid crimson;
    background-color: black;
    color: white;
}

.title__startPage {
    font-size: 75px;
    font-weight: bold;
    display: flex;
    justify-content: center;
}

.header {
    display: flex;
    justify-content: center;
    margin-top: 40px;
}

.count__block {
    display: block;
}